@import "../../../data/styles.css";

/* Modal Styles */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
  z-index: 9999;
  transition: all 0.35s ease-in-out;
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  overflow: auto;
  border-radius: 3px;

}

.loader{
  margin: 0 0 2em;
  height: 100px;
  width: 20%;
  text-align: center;
  padding: 1em;
  margin: 0 auto 1em;
  display: inline-block;
  vertical-align: top;
}

/*
  Set the color of the icon
*/
.loader svg path,
.loader svg rect{
  fill: var(--link-color);
}
.pdfViewer{
    display: flex;
    justify-content: center;
    align-items: center;
}
 .close {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  border: 0;
  transition: all 0.45s ease-in-out 0.85s;
  display: flex;
  justify-content: center;
  border-radius: 5%;
}
.resume{
    height: auto;
    max-height: 600px;
}
.react-pdf__Page__canvas {
    margin: 0 auto;
    width: 60% !important;
    height: 60% !important;
}
.react-pdf__Page{
    margin: 0 auto;
    max-width: 60%!important;
    max-height: 60%!important;
}
button {
  margin: 20px 0;
}
.custom-btn {
  width: 250px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-weight: 500;
  font-size: 16px;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
   box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
  display: flex;
  justify-content: center;
}

button a {
    text-decoration: none;
    color:var(--link-color);
}
.btn-1 {
  background: rgb(247,150,192);
background: radial-gradient(circle, rgba(247,150,192,1) 0%, rgba(118,174,241,1) 100%);
  line-height: 42px;
  padding: 0;
  border: none;
}
.btn-1 span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.btn-1:before,
.btn-1:after {
  position: absolute;
  content: "";
  height: 0%;
  width: 1px;
 box-shadow:
   -1px -1px 20px 0px rgba(255,255,255,1),
   -4px -4px 5px 0px rgba(255,255,255,1),
   7px 7px 20px 0px rgba(0,0,0,.4),
   4px 4px 5px 0px rgba(0,0,0,.3);
}
.btn-1:before {
  right: 0;
  top: 0;
  transition: all 500ms ease;
}
.btn-1:after {
  left: 0;
  bottom: 0;
  transition: all 500ms ease;
}
.btn-1:hover{
  background: transparent;
  color: #76aef1;
  box-shadow: none;
}
.btn-1:hover:before {
  transition: all 500ms ease;
  height: 100%;
}
.btn-1:hover:after {
  transition: all 500ms ease;
  height: 100%;
}
.btn-1 span:before,
.btn-1 span:after {
  position: absolute;
  content: "";
  box-shadow:
   -1px -1px 20px 0px rgba(255,255,255,1),
   -4px -4px 5px 0px rgba(255,255,255,1),
   7px 7px 20px 0px rgba(0,0,0,.4),
   4px 4px 5px 0px rgba(0,0,0,.3);
}
.btn-1 span:before {
  left: 0;
  top: 0;
  width: 0%;
  height: .5px;
  transition: all 500ms ease;
}
.btn-1 span:after {
  right: 0;
  bottom: 0;
  width: 0%;
  height: .5px;
  transition: all 500ms ease;
}
.btn-1 span:hover:before {
  width: 100%;
}
.btn-1 span:hover:after {
  width: 100%;
}

.btn-2 {
  border: none;
  background: var(--link-color);
    /* background: linear-gradient(0deg, rgba(251,33,117,1) 0%, rgba(234,76,137,1) 100%); */
    color: var(--quaternary-color);
    overflow: hidden;
}
.btn-2:hover {
    text-decoration: none;
    color: #fff;
}
.btn-2:before {
    position: absolute;
    content: '';
    display: inline-block;
    top: -180px;
    left: 0;
    width: 30px;
    height: 100%;
    background-color: #fff;
    animation: shiny-btn1 3s ease-in-out infinite;
}
.btn-2:hover{
  opacity: .7;
}
.btn-2:active{
  box-shadow:  4px 4px 6px 0 rgba(255,255,255,.3),
              -4px -4px 6px 0 rgba(116, 125, 136, .2), 
    inset -4px -4px 6px 0 rgba(255,255,255,.2),
    inset 4px 4px 6px 0 rgba(0, 0, 0, .2);
}


@keyframes shiny-btn1 {
    0% { -webkit-transform: scale(0) rotate(45deg); opacity: 0; }
    80% { -webkit-transform: scale(0) rotate(45deg); opacity: 0.5; }
    81% { -webkit-transform: scale(4) rotate(45deg); opacity: 1; }
    100% { -webkit-transform: scale(50) rotate(45deg); opacity: 0; }
}